.container {
    text-align: center;
}

.chat_container {
    overflow-y: auto;
    width: 50%;
    height: 50vh;
    margin: 0 auto;
    border-radius: 10px;
    border: 1px solid black;
}

.msg {
    padding: 10px;
}

.sender_text {
    width: 40%;
    padding: 10px;
    border-radius: 10px;
    background-color: #ececec;
}

.recipient_text {
    margin-left: auto;
    margin-right: 0;
    width: 40%;
    word-break: break-all;
    padding: 10px;
    border-radius: 10px;
    color: white;
    background-color: black;
}

.input {
    padding: 10px;
    width: 100%;
    border-radius: 10px;
}

.btn_container {
    padding: 10px 0;
}

.btn {
    width: 50%;
    padding: 15px 25px;
    border: 0;
    border-radius: 10px;
    color: white;
    font-weight: bolder;
    font-size: 20px;
    background-color: #18b13f;
}

.text_container {
    display: flex;
    width: 50%;
    margin: 0 auto;
}

.send_container {
    padding: 10px 0;
}