.container {
    width: 50%;
    margin: 0 auto;
}

.form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

input {
    padding: 10px 20px;
    border-radius: 10px;
}

label {
    padding: 10px 0;
}

input[type='submit'] {
    margin: 10px 0;
    border: 0;
    background-color: #000000;
    color: white;
    font-size: 20px;
    font-weight: bolder;
}